package nowiwr01p.ton.together.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.fillMaxSize
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.forms.Button
import nowiwr01p.ton.together.TonConnectUI
import nowiwr01p.ton.together.TonConnectUiCreateOptions
import org.jetbrains.compose.web.dom.Text

@Page
@Composable
fun HomePage() {
    TonConnectButton()
}

@Composable
fun TonConnectButton() {
    val ui = remember {
        val json = kotlin.js.json(
            "manifestUrl" to "https://nowiwr01p.com/public/tonconnect-manifest.json"
        )
        TonConnectUI(json.unsafeCast<TonConnectUiCreateOptions>())
    }
    Box(
        modifier = Modifier.fillMaxSize(),
        contentAlignment = Alignment.Center
    ) {
        Button(onClick = {
            ui.connectWallet()
        }) {
            Text("Connect Wallet")
        }
    }
}